'use strict';

const wall = document.querySelector('.wall');
const spider = document.querySelector('.spider');

const wallStyle = getComputedStyle(wall);
const spiderStyle = getComputedStyle(spider);

const topPosition = `
  ${(parseFloat(wallStyle.height)
    - parseFloat(spiderStyle.height)) / 2}px
`;
const leftPosition = `${
  (parseFloat(wallStyle.width)
    - parseFloat(spiderStyle.width)) / 2}px
`;

spider.style.top = topPosition;
spider.style.left = leftPosition;
